"use client";

import React from "react";
import { useTheme } from "next-themes";
import Link from "next/link";

interface ModuleCardProps {
  title: string;
  status: "completed" | "active" | "locked";
  href?: string; // Propiedad para la URL del módulo
}

const ModuleCard: React.FC<ModuleCardProps> = ({ title, status, href }) => {
  const { theme } = useTheme();

  const getStyles = () => {
    switch (status) {
      case "completed":
        return "module module-card-completed";
      case "active":
        return "module module-card-active cursor-pointer";
      case "locked":
        return "module module-card-locked cursor-not-allowed";
      default:
        return "";
    }
  };

  const getIconSrc = () => {
    switch (status) {
      case "completed":
        return theme === "dark"
          ? "/assets/dark/check.svg"
          : "/assets/light/check.svg";
      case "active":
        return theme === "dark"
          ? "/assets/dark/lock-open.svg"
          : "/assets/light/lock-open.svg";
      case "locked":
        return theme === "dark"
          ? "/assets/dark/lock.svg"
          : "/assets/light/lock.svg";
      default:
        return "";
    }
  };

  return (
    <Link href={status === "locked" ? "#" : href || ""}>
      <div
        className={`flex items-center justify-between p-4 rounded-lg shadow-sm transition-transform duration-300 hover:scale-105 ${getStyles()}`}
        aria-disabled={status === "locked"}
      >
        <span className="font-medium">{title}</span>
        <img src={getIconSrc()} alt={status} className="module-icon" />
      </div>
    </Link>
  );
};

export default ModuleCard;
