import React from "react";
import Image from "next/image";

interface Level {
  id: number;
  label: string;
  completedIcon: string; // Imagen para "completado" e "in-progress"
  lockedIcon?: string; // Imagen para "locked" (opcional en el nivel 1)
  state: "in-progress" | "completed" | "locked";
}

interface UserProgressProps {
  username: string;
  levels: Level[];
}

const UserProgress: React.FC<UserProgressProps> = ({ username, levels }) => {
  return (
    <div className="user-progress flex flex-col items-center gap-6 mb-6">
      {/* Encabezado de bienvenida */}
      <div className="welcome text-center">
        <h1 className="text-2xl font-bold text-primary-900">Bienvenido(a)</h1>
        <h2 className="text-3xl font-extrabold text-primary">{username}</h2>
      </div>

      {/* Barra de progreso de niveles */}
      <div className="levels flex items-center justify-between w-full max-w-4xl relative">
        {levels.map((level, index) => (
          <div
            key={level.id}
            className="flex items-center justify-center relative"
          >
            {/* Icono del nivel */}
            <div
              className={`level__icon-container ${
                level.state === "locked" ? "locked" : "active"
              }`}
            >
              <Image
                src={
                  level.state === "locked" && level.lockedIcon
                    ? level.lockedIcon
                    : level.completedIcon
                }
                alt={level.label}
                width={60}
                height={60}
                className="level__icon"
              />
            </div>

            {/* Línea de conexión entre niveles */}
            {index < levels.length - 1 && (
              <div
                className={`level__line ${
                  levels[index + 1].state === "locked" ? "locked" : "active"
                }`}
              />
            )}
          </div>
        ))}
      </div>
    </div>
  );
};

export default UserProgress;
