"use client";

import React from "react";
import ModuleCard from "../../progress/components/ModuleCard";
import UserProgress from "../../progress/components/UserProgress"; // Componente de progreso

// Variables de estados para mantener el código limpio
const LEVEL_STATE = {
  COMPLETED: "completed",
  IN_PROGRESS: "in-progress",
  LOCKED: "locked",
};

const ProgressPage: React.FC = () => {
  // Datos dinámicos del usuario
  const user = {
    name: "Camila Gómez",
    levels: [
      {
        id: 1,
        label: "Principiante",
        completedIcon: "/assets/level-1-completed.png",
        lockedIcon: undefined, // No aplica para el primer nivel
        state: LEVEL_STATE.COMPLETED, // Nivel completado
        subtitle: "Nivel A1",
        modules: [
          {
            id: "module-1",
            title: "Getting Started: The English Alphabet",
            status: LEVEL_STATE.COMPLETED,
            icon: "/assets/modules/module-1.png",
          },
          {
            id: "module-2",
            title: "Greetings and Basic Politeness",
            status: LEVEL_STATE.IN_PROGRESS,
            icon: "/assets/modules/module-2.png",
          },
          {
            id: "module-3",
            title: "Introducing Yourself and Others",
            status: LEVEL_STATE.LOCKED,
            icon: "/assets/modules/module-3-locked.png",
          },
        ],
      },
      {
        id: 2,
        label: "Intermedio",
        completedIcon: "/assets/level-2-completed.png",
        lockedIcon: "/assets/level-2-locked.png",
        state: LEVEL_STATE.IN_PROGRESS,
        subtitle: "Nivel A2",
        modules: [
          {
            id: "module-4",
            title: "Building Longer Sentences",
            status: LEVEL_STATE.LOCKED,
            icon: "/assets/modules/module-4-locked.png",
          },
          {
            id: "module-5",
            title: "Describing Daily Routines",
            status: LEVEL_STATE.LOCKED,
            icon: "/assets/modules/module-5-locked.png",
          },
        ],
      },
      {
        id: 3,
        label: "Avanzado",
        completedIcon: "/assets/level-3-completed.png",
        lockedIcon: "/assets/level-3-locked.png",
        state: LEVEL_STATE.LOCKED,
        subtitle: "Nivel B1",
        modules: [
          {
            id: "module-6",
            title: "Mastering Past Tenses",
            status: LEVEL_STATE.LOCKED,
            icon: "/assets/modules/module-6-locked.png",
          },
          {
            id: "module-7",
            title: "Talking About Future Plans",
            status: LEVEL_STATE.LOCKED,
            icon: "/assets/modules/module-7-locked.png",
          },
        ],
      },
    ],
  };

  return (
    <div className="p-6">
      {/* Barra de progreso dinámico */}
      <UserProgress username={user.name} levels={user.levels} />

      {/* Tarjetas de módulos por nivel */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
        {user.levels.map((level) => (
          <div key={level.id} className="flex flex-col gap-4">
            <h2 className="text-lg font-bold text-primary">{level.label}</h2>
            <p className="text-sm text-neutral-600">{level.subtitle}</p>
            <div className="flex flex-col gap-2">
              {level.modules.map((module) => (
                <ModuleCard
                  key={module.id}
                  title={module.title}
                  status={module.status}
                  icon={module.icon} // Icono dinámico del módulo
                  href={
                    module.status !== LEVEL_STATE.LOCKED
                      ? `/dashboard/progress/module/${module.id}`
                      : undefined
                  }
                />
              ))}
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default ProgressPage;
